.aboutMeContainer {
  background-color: rgba(255, 255, 255, 0.6);
  overflow: hidden;
  border-radius: 10px;
}

.aboutMeContainer p {
  margin: 3%;
  font-size: 30px;
  color: #000000;
  height: 30%;
  text-align: center;
  overflow: hidden;
}

.aboutMeContainer h2 {
  text-align: center;
}

.bioLink {
  text-decoration: none;
}

.bioLink:hover {
  text-shadow: 0px 0px 1px black;
}

.nameText {
  color: purple;
}

.bioPicture {
  width: 20%;
  margin: 10px;
  float: left;
  vertical-align: middle;
  border-radius: 5px;
}

@media (max-width:500px) {
  .bioPicture {
    width: 50%;
    float: none;
    margin: 10px;
  }
}
