@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@300&display=swap);
body {
  background-color: grey;
  background-image: url(/static/media/background.d9092ba7.jpg);
  background-repeat: round;
  background-size: 100%;
  background-attachment: fixed;
}
.App {
}

.SectionContainer {
  box-sizing: border-box;
  margin: auto;
  height: 80%;
  padding: 10px;
  font-family: 'Roboto', sans-serif;
}

.aboutMeContainer {
  background-color: rgba(255, 255, 255, 0.6);
  overflow: hidden;
  border-radius: 10px;
}

.aboutMeContainer p {
  margin: 3%;
  font-size: 30px;
  color: #000000;
  height: 30%;
  text-align: center;
  overflow: hidden;
}

.aboutMeContainer h2 {
  text-align: center;
}

.bioLink {
  text-decoration: none;
}

.bioLink:hover {
  text-shadow: 0px 0px 1px black;
}

.nameText {
  color: purple;
}

.bioPicture {
  width: 20%;
  margin: 10px;
  float: left;
  vertical-align: middle;
  border-radius: 5px;
}

@media (max-width:500px) {
  .bioPicture {
    width: 50%;
    float: none;
    margin: 10px;
  }
}


h1 {
  line-height: 1.2;
}

.jobContainer {
  background-color: rgba(255, 255, 255, 0.6);
  overflow: hidden;
  border-radius: 10px;
  margin: 10px;
}

.infoContainer {
  margin: 10px;
}

.projectContainer {
  background-color: rgba(255, 255, 255, 0.6);
  overflow: hidden;
  border-radius: 10px;
  margin: 10px;
}

.projectContainer img {
    max-width: 50%;
    float: left;
    margin: 5px;
    max-height: 300px;
}

.infoContainer {
  margin: 10px;
}

.infoContainer h3 {
  float: right;
  margin-top: 0;
}

.linkButton {
  float: right;
  border: solid;
  border-width: 2px;
  border-radius: 5px;
  border-color: grey;
  background-color: grey;
  margin-left: 15px;
}

.linkButton a {
  text-decoration: none;
  color: black;
}

.linkButton a:hover {
  color: darkGrey;
}

.description {
  margin: 10px;
  font-size: 20px;
  text-align: center;
}

.contactContainer {
  background-color: rgba(255, 255, 255, 0.6);
  overflow: hidden;
  border-radius: 10px;
  text-align: center;
  -webkit-align-content: center;
          align-content: center;
}

.contactContainer h1 {
  text-align: center;
}

.contactButton {
  border: solid;
  border-width: 2px;
  border-radius: 5px;
  border-color: grey;
  background-color: grey;
  margin: auto;
  margin-top: 10px;
  margin-bottom: 10px;
  max-width: 300px;
}

.contactButton a {
  text-decoration: none;
  color: black;
}

.contactButton a:hover {
  color: darkGrey;
}

