.projectContainer {
  background-color: rgba(255, 255, 255, 0.6);
  overflow: hidden;
  border-radius: 10px;
  margin: 10px;
}

.projectContainer img {
    max-width: 50%;
    float: left;
    margin: 5px;
    max-height: 300px;
}

.infoContainer {
  margin: 10px;
}

.infoContainer h3 {
  float: right;
  margin-top: 0;
}

.linkButton {
  float: right;
  border: solid;
  border-width: 2px;
  border-radius: 5px;
  border-color: grey;
  background-color: grey;
  margin-left: 15px;
}

.linkButton a {
  text-decoration: none;
  color: black;
}

.linkButton a:hover {
  color: darkGrey;
}

.description {
  margin: 10px;
  font-size: 20px;
  text-align: center;
}
