.contactContainer {
  background-color: rgba(255, 255, 255, 0.6);
  overflow: hidden;
  border-radius: 10px;
  text-align: center;
  align-content: center;
}

.contactContainer h1 {
  text-align: center;
}

.contactButton {
  border: solid;
  border-width: 2px;
  border-radius: 5px;
  border-color: grey;
  background-color: grey;
  margin: auto;
  margin-top: 10px;
  margin-bottom: 10px;
  max-width: 300px;
}

.contactButton a {
  text-decoration: none;
  color: black;
}

.contactButton a:hover {
  color: darkGrey;
}
